import React from "react";
import type { ElementItem } from "../../reducers";
import Icon from "../Icon";

import styles from "./index.module.scss";

interface SettingProps {
  checked?: string;
  dispatch: any;
  data?: Record<string, any>;
  config?: Record<string, any>;
  element?: ElementItem;
  onClose: (arg: boolean) => void;
  properties: React.CSSProperties;
  visible: boolean;
}

const ReactCSSPropertiesZhCn: {
  [key: string]: string;
} = {
  backgroundImage: "背景图片",
  backgroundSize: "背景尺寸",
  backgroundColor: "背景颜色",
  backgroundRepeat: "平铺模式",
  backgroundPosition: "背景位置",
  fontFamily: "字体",
  fontSize: "字体大小",
  borderWidth: "边框宽度",
  borderStyle: "边框样式",
  borderColor: "边框颜色",
  borderRadius: "边框圆角",
  width: "宽度",
  height: "高度",
  top: "y轴位置",
  left: "x轴位置",
};

const DataPropertiesZhCn: {
  [key: string]: string;
} = {
  total: "合计",
  unit: "单位",
  title: "标题",
  leftTitle: "左标题",
  rightTitle: "右标题",
};

const isNumbers = [
  "top",
  "left",
  "width",
  "height",
  "borderRadius",
  "borderWidth",
  "fontSize",
];

const Setting: React.FC<SettingProps> = (props) => {
  const {
    visible,
    checked,
    data,
    config = {},
    properties,
    element,
    dispatch,
    onClose,
  } = props;

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement>,
    key: string
  ) => {
    if (checked) {
      dispatch({
        type: "update",
        payload: {
          key: checked,
          draggingElement: {
            ...element,
            properties: {
              ...properties,
              [key]: isNumbers.includes(key)
                ? Number(e.target.value) / 2
                : e.target.value,
            },
          },
        },
      });
    } else {
      dispatch({
        type: "backdrop",
        payload: {
          [key]: isNumbers.includes(key)
            ? Number(e.target.value) / 2
            : e.target.value,
        },
      });
    }
  };

  const handleChangeConfig = (
    e: React.ChangeEvent<HTMLInputElement>,
    key: string
  ) => {
    dispatch({
      type: "update",
      payload: {
        key: checked,
        draggingElement: {
          ...element,
          config: {
            ...config,
            [key]: e.target.value,
          },
        },
      },
    });
  };

  const handleChangeData = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    try {
      const nextData = JSON.parse(e.target.value);
      dispatch({
        type: "update",
        payload: {
          key: checked,
          draggingElement: {
            ...element,
            data: nextData,
          },
        },
      });
    } catch (error) {
      console.error("please enter json string!");
    }
  };

  return (
    <div
      className={styles.setting}
      style={visible ? { right: 0 } : {}}
      onClick={(e: React.MouseEvent<HTMLDivElement>) => {
        e.stopPropagation();
      }}
    >
      <div className={styles.putAway} onClick={() => onClose(!visible)}>
        <Icon
          type="left"
          style={{
            color: "#bcc8d4",
            fontSize: 18,
            transform: `rotateY(${visible ? "180deg" : "0"})`,
            transition: "all 0.25s ease-in-out",
          }}
        />
      </div>
      <div className={styles.body}>
        {data && (
          <div className={styles.item} style={{ height: 100 }} key="data">
            <label>示例数据: </label>
            <textarea
              value={JSON.stringify(data)}
              name="data"
              onChange={(e) => handleChangeData(e)}
            />
          </div>
        )}
        {Object.entries(config).map(([key, value]) => (
          <div className={styles.item} key={key}>
            <label>{DataPropertiesZhCn[key] || key}: </label>
            <input
              value={value}
              type="text"
              name={toString()}
              onChange={(e) => handleChangeConfig(e, key)}
            />
          </div>
        ))}
        {Object.entries(properties).map(([key, value]) => (
          <div className={styles.item} key={key}>
            <label>{ReactCSSPropertiesZhCn[key] || key}: </label>
            <input
              value={typeof value === "number" ? value * 2 : value}
              type="text"
              name={toString()}
              onChange={(e) => handleChange(e, key)}
            />
          </div>
        ))}
      </div>
    </div>
  );
};

export default Setting;
